@import "~antd/dist/antd.css";

.app {
  margin: 20px 50px;
}

@media (max-width: 768px) {
  .app {
    margin: 20px 25px;
  }
}

.item {
  margin-bottom: 15px;
}

.ant-card {
  padding: 10px;
}

.ant-card-body {
  padding-left: 0;
  padding-right: 0;
}

.ant-card-hoverable:hover {
  z-index: 2;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
